/* CONTACT INFO */
.contact-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 3rem;
    height: 50vh;
    width: 100%;
    
    background: #c2bfbf;
    text-align: justify;
}

.contact-info h4, .contact-info ul, .contact-info p {
    text-align: center;
    margin: 0 0 1rem 0;
}
.contact-info i{
    color: #f29778;
}

.mensaje{
    display: flex;
    flex-direction: column;
}
.archivos{
    margin-left: 1rem;
}
.evidencia{
    padding-top: 2rem;
}

/* :::::::::::::::::::::::::::::::::::::::::::: RESPONSIVE: tableta ::::::::::::::::::::::::::::::::::::::::::::::::::::::::: */
@media screen and (min-width:768px){
    .contactenos{
        font-size: 2vw;
        padding: 20px;
    }

    .contact-P {
        display: grid;
        grid-template-columns: 1fr 2fr;
        line-height: 1.6rem;
        
    }
    .contact-P > * {
        padding: 2em;

    }
    .contact-info h4,
    .contact-info ul,
    .contact-info p {
        text-align: left;
    }
    .contact-info{
        height: 100vh;
    }
}

/* :::::::::::::::::::::::::::::::::::::::::::: RESPONSIVE: medio ::::::::::::::::::::::::::::::::::::::::::::::::::::::::: */
@media screen and (min-width:1024px) {
    .contactenos{
        font-size: 0.5vw;
        padding: 20px;
    }
}